import React from 'react';
import portfolioData from '../data/portfolio.json';
import { alphaSort } from '../alphaSort';

export const Portfolio: React.FC = () => {
    return(
        <main className='my-10'>
            <h1 className='text-center text-5xl'>Portfolio</h1>
            <table>
                {portfolioData.sort((a, b) => b.difficulty - a.difficulty).map(app => <ProjectRow row={app}/>)}
            </table>
        </main>
    );
}

type projectProps = {
    row: {
        name: string;
        description: string;
        image: string;
        programType: string;
        links: {
            name: string;
            link: string;
        }[];
    } | {
        name: string;
        description: string;
        programType: string;
        links: {
            name: string;
            link: string;
        }[];
        image?: undefined;
    }
}

const ProjectRow: React.FC<projectProps> = ({row}) => {
    const image = getImage(row.image);

    const RowImage = () => {
        return(
            <tr>
                <td className='w-1/4 py-5'>
                    <img className='mx-auto' src={image}/>
                </td>
                <td className='pl-20 py-5'>
                    <h2>{row.name}</h2>
                    <p>{row.description}</p>
                    <h3>{row.links.map(link => <h3>{link.name} <a href={link.link} target='_blank' rel='noopener norefferrer'>{link.link}</a></h3>)}</h3>
                </td>
            </tr>
        )
    }

    const RowDefault = () => {
        return(
            <tr>
                <td colSpan={2} className='py-5'>
                    <h2>{row.name}</h2>
                    <p>{row.description}</p>
                    <h3>{row.links.map(link => <h3>{link.name} <a href={link.link} target='_blank' rel='noopener norefferrer'>{link.link}</a></h3>)}</h3>
                </td>
            </tr>
        )
    }

    return(
        image ? <RowImage/> : <RowDefault/>
    );
}

const getImage = (image: string | undefined) => {
    try{
        return require(`../images/project-img/${image}`)
    }
    catch(err){
        return null
    }
}