import React from 'react';
import {Routes, Route, Link} from 'react-router-dom';
import './App.css';
import {Home, Resume, Portfolio, Technologies} from './components';
import 'bootstrap-icons/font/bootstrap-icons.css';

function App() {
  return (
    <div className='container mx-auto my-10'>
      <header className='flex justify-between items-center border-b sticky top-0 py-10'>
        <h1 id='name' className='grow'>Ryan Proctor</h1>
        <nav className='flex justify-between grow'>
          <Link className='underline-ani' to="/">Home</Link>
          <Link className='underline-ani' to="/portfolio">Portfolio</Link>
          <Link className='underline-ani' to="/technologies">Technologies</Link>
          <Link className='underline-ani' to="/resume">Resume</Link>
        </nav>
      </header>
      <Routes>
        <Route path="/" element={<Home/>}/>
        <Route path="/resume" element={<Resume/>}/>
        <Route path="/technologies" element={<Technologies/>}/>
        <Route path="/portfolio" element={<Portfolio/>}/>
      </Routes>
      <footer className='flex justify-around border-t p-10'>
        <a href='mailto: rydproctor@gmail.com' className='underline-ani' target='_blank' rel='noopener norefferrer'><i className="bi bi-envelope-fill"></i> rydproctor@gmail.com</a>
        <a href='tel:408-335-3379' className='underline-ani' target='_blank' rel='noopener norefferrer'><i className="bi bi-phone-fill"></i>(408) 335-3379</a>
        <a href='https://www.linkedin.com/in/ryan-drew-proctor/' className='underline-ani' target='_blank' rel='noopener norefferrer'><i className="bi bi-linkedin"></i> LinkedIn</a>
        <a href='https://github.com/rproct' className='underline-ani' target='_blank' rel='noopener norefferrer'><i className="bi bi-github"></i> GitHub</a>
      </footer>
    </div>
  );
}

export default App;
