import React from 'react';
import matrix from '../data/skillMatrix.json';
import experience from '../data/experience.json';
import education from '../data/education.json';

export const Resume: React.FC = () => {
    const skillList = ["Game Development", "Games", "User Interface Design", "2D Animation Design", "Game Design", "Computer Science", "Software Development", "Deployment", 
                       "Software Engineering", "C / C++", "Java", ".Net", "Python", "Databases", "SQL and MySQL", "Android",
                       "Linux", "Unity Engine", "Visual Studio", "Adobe Creative Cloud", "Maya", "Node.JS", "JavaScript/TypeScript", "Web Development"];
    // const pdf = require('./static/media/Ryan_Proctor_Resume.2bb81f9f6b9f7ab01dff.pdf');

    return(
        <main className='my-10'>
            <h1 className='text-center text-5xl'>Resume</h1>
            {/* <div className='flex justify-center'>
                <a className='bg-white text-black p-3 rounded-3xl' href={pdf} download='Ryan-Proctor-Resume.pdf'><b>Download Resume</b></a>
            </div> */}
            <div className='flex justify-between'>
                <div className='w-2/5'>
                    <h1 className='text-center'>Objective</h1>
                    <p className='mx-10 py-5'>
                        Motivated and passionate computer science and communication design graduate from California State University Monterey Bay with over a year of teacher's assistant experience in mentoring fellow students and grading programs. Seeking to apply proven skills in C++, Python, Java, JavaScript, and C#.
                    </p>
                </div>

                <div className='w-2/5'>
                    <table>
                        <tr>
                            <th colSpan={2}>
                                <h1>Education</h1>
                            </th>
                        </tr>
                        {education.map(edu => <EduRow row={edu}/>)}
                    </table>
                </div>
            </div>

            <table>
                <tr>
                    <th colSpan={2}><h1 className='text-center'>Experience</h1></th>
                </tr>
                {experience.map(xp => <ExperienceRow row={xp}/>)}
            </table>

            <div className='pb-32 border-b'>
                <h1 className='text-center'>Skills</h1>
                <ul className='grid grid-cols-3'>
                    {skillList.map(skill => <li className='mx-auto'>{skill}</li>)}
                </ul>
            </div>

            <h1 className='text-center text-5xl mt-32'>Techincal Skills</h1>
            <table className='mx-auto'>
                <tr className='border-b'>
                    <th>
                        <h2>Project/Classes</h2>
                    </th>
                    <th>
                        <h2>Skills</h2>
                    </th>
                </tr>
                {matrix.map((mat, i, arr) => <SkillRow row={mat} isLast={arr.length === i + 1}/>)}
            </table>
        </main>
    );
}

type EduProps = {
    row: {
        school: string;
        yearBegin: number;
        yearEnd: number;
        degrees: string[];
    };
}

const EduRow:React.FC<EduProps> = ({row}) => {
    return(
        <tr>
            <th className='p-5 align-top'>{row.yearBegin} - {row.yearEnd}</th>
            <td className='py-5'>
                <b>{row.school}</b>
                <ul className='list-inside'>{row.degrees.map(degree => <li>{degree}</li>)}</ul>
            </td>
        </tr>
    );
}

type ExperienceProps = {
    row: {
        company: string;
        job: string;
        year: number;
        duties: string[];
    } | {
        company: string;
        year: number;
        duties: string[];
        job?: undefined;
    };
}

const ExperienceRow:React.FC<ExperienceProps> = ({row}) => {
    return(
        <tr>
            <th className='w-1/4 align-top'>{row.year}</th>
            <td className='pb-5'>
                <b className='align-top'>{row.company}</b>
                {row.job !== undefined && <p>{row.job}</p>}
                <ul className='list-inside'>
                    {row.duties.map(duties => <li>{duties}</li>)}
                </ul>
            </td>
        </tr>
    );
}

type SkillProps = {
    row: {
        project: string;
        skills: string;
        link?: undefined;
    } | {
        project: string;
        skills: string;
        link: string;
    };
    isLast: boolean;
}

const SkillRow: React.FC<SkillProps> = ({row, isLast}) => {
    return(
        <tr className={isLast ? '' : 'border-b'}>
            <th className='p-10'>{row.project}</th>
            <td className='py-5'><ul className='mx-auto grid md:grid-cols-2 lg:grid-cols-4 gap-4'>{row.skills.split(', ').map(skill => <li>{skill}</li>)}</ul></td>
        </tr>
    );
}