import React, { useState } from 'react';
import skills from '../data/skills.json';
import { alphaSort } from '../alphaSort';

//grid sm:grid-cols-2 md:grid-cols-4 lg:grid-cols-6 xl:grid-cols-8 gap-20
export const Technologies: React.FC = () => {
    const [selected, setSelected] = useState('experience');

    const getSelected = (e: any) => {
        setSelected(e.target.value);
    }

    return(
        <main className='my-10'>
            <h1 className='text-center text-5xl'>Technologies Used</h1>
            <div className='flex justify-center'>
                <div className='text-2xl'>
                    Sort By: <select className='appearance-none border-b outline-none focus:border-b-2 bg-transparent border-none py-0 pl-0 text-2xl' onChange={getSelected}>
                        <option selected value="experience">Experience</option>
                        <option value="alpha">Alphabetical</option>
                    </select>
                </div>
            </div>
            {selected === 'experience' && <div className='flex flex-wrap justify-center'>
                {skills.sort((a, b) => alphaSort(a.language, b.language)).sort((a, b) => a.year - b.year).map((skill) => <SkillTemplate language={skill.language} svg={skill.svg} year={skill.year}/>)}
            </div>}
            {selected === 'alpha' && <div className='flex flex-wrap justify-center'>
                {skills.sort((a, b) => alphaSort(a.language, b.language)).map((skill) => <SkillTemplate language={skill.language} svg={skill.svg} year={skill.year}/>)}
            </div>}
        </main>
    );
}

type Props = {
    svg: string;
    language: string;
    year: number;
}

const SkillTemplate: React.FC<Props> = ({svg, language, year}) => {
    const logo = getSVG(svg);
    const date = new Date;
    const known = date.getFullYear() - year;

    return(
        <figure className='m-10'>
            <figcaption>
                {logo ? <img src={logo} alt={svg} className='h-20 mx-auto my-3'/> : <h6>{svg}.svg</h6>}
                <h2 className='text-center'>{language}</h2>
                <h6 className='text-center'>{year}</h6>
                <h6 className='text-center'>
                    {known > 0 ? known : '>1'} year{known > 1 ? 's' : ''} Experience
                </h6>
            </figcaption>
        </figure>
    )
}

const getSVG = (svg: string) => {
    try{
        return require(`../images/language-logos/${svg}.svg`);
    }
    catch(err){
        return null;
    }
}